img {
	border: 0; }

legend {
	border: 0;
	padding: 0; }

td, th {
	padding: 0; }

html {
	box-sizing: border-box;
	font-family: sans-serif;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%; }

body {
	margin: 0; }

article, aside, details, figcaption, figure, footer, header, main, menu, nav, section, summary {
	display: block; }

audio, canvas, progress, video {
	display: inline-block;
	vertical-align: baseline; }

audio:not([controls]) {
	display: none;
	height: 0; }

[hidden], template {
	display: none; }

a {
	background-color: transparent;
	&:active, &:hover {
		outline: 0; } }

abbr[title] {
	border-bottom: 1px dotted; }

b, optgroup, strong {
	font-weight: 700; }

dfn {
	font-style: italic; }

h1 {
	font-size: 2em;
	margin: .67em 0; }

mark {
	background: #ff0;
	color: #000; }

small {
	font-size: 80%; }

sub {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline; }

sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
	top: -.5em; }

sub {
	bottom: -.25em; }

svg:not(:root) {
	overflow: hidden; }

figure {
	margin: 1em 40px; }

hr {
	box-sizing: content-box;
	height: 0; }

pre, textarea {
	overflow: auto; }

code, kbd, pre, samp {
	font-family: monospace,monospace;
	font-size: 1em; }

button, input, optgroup, select, textarea {
	color: inherit;
	font: inherit;
	margin: 0; }

button {
	overflow: visible;
	text-transform: none; }

select {
	text-transform: none; }

button, html input[type=button] {
	-webkit-appearance: button;
	cursor: pointer; }

input {
	&[type=reset], &[type=submit] {
		-webkit-appearance: button;
		cursor: pointer; } }

button[disabled], html input[disabled] {
	cursor: default; }

button::-moz-focus-inner {
	border: 0;
	padding: 0; }

input {
	&::-moz-focus-inner {
		border: 0;
		padding: 0; }
	line-height: normal;
	&[type=checkbox], &[type=radio] {
		box-sizing: border-box;
		padding: 0; }
	&[type=number] {
		&::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
			height: auto; } }
	&[type=search] {
		-webkit-appearance: textfield;
		box-sizing: content-box;
		&::-webkit-search-cancel-button, &::-webkit-search-decoration {
			-webkit-appearance: none; } } }

fieldset {
	border: none;
	margin: 0;
	padding: 0; }

table {
	border-collapse: collapse;
	border-spacing: 0; }

.clear {
	clear: both;
	display: block;
	overflow: hidden;
	visibility: hidden;
	width: 0;
	height: 0;

	&:after {
		clear: both;
		display: block;
		overflow: hidden;
		visibility: hidden;
		width: 0;
		height: 0; } }

ul, ol {
	list-style: none;
	margin: 0;
	padding: 0; }

* {
	max-width: 100%; }

*, *:before, *:after {
	box-sizing: inherit; }

img {
	box-sizing: content-box; }
